import { Button } from "@webs/component-library/button";

import { addCodeExample } from "./helpers/code-example";

export default {
  title: "Example/Button",
  argTypes: {
    hoverColor: {
      control: {
        type: "select",
        options: ["default", "violet", "pink"],
      },
    },
    title: {
      control: {
        type: "select",
        options: ["Short", "Medium Length", "Rather Long Button Text"],
      },
    },
    link: {
      url: {
        href: {
          type: "text",
        },
      },
    },
  },
};

const Template = (module) => {
  return `
    <webs-button
      text=${module.title}
      extraClasses="${module.button_color}"
      href="${module.link.url.href}"
      openInNewTab="true"
    ></webs-button>
  `;
};

export const Primary = Template.bind({
  hoverColor: "default",
  text: "Short",
});

